import { Injectable, inject } from '@angular/core';
import { firstValueFrom } from 'rxjs';
import { AccountsService, VendorResponse } from '../../../api';
import { EntityDataStrategy } from './entity-data.strategy';

@Injectable({ providedIn: 'root' })
export class VendorDataStrategy implements EntityDataStrategy<VendorResponse> {
  private readonly accountsService = inject(AccountsService);

  async fetchById(id: string): Promise<VendorResponse> {
    try {
      return await firstValueFrom(this.accountsService.getVendor(id));
    } catch (error) {
      throw new Error(
        `Failed to fetch vendor: ${error instanceof Error ? error.message : String(error)}`,
      );
    }
  }

  async update(
    id: string,
    data: Partial<VendorResponse>,
  ): Promise<VendorResponse> {
    try {
      return await firstValueFrom(this.accountsService.updateVendor(id, data));
    } catch (error) {
      throw new Error(
        `Failed to update vendor: ${error instanceof Error ? error.message : String(error)}`,
      );
    }
  }
}
