import { Injectable, inject } from '@angular/core';
import { firstValueFrom } from 'rxjs';
import { AccountsService, ClientResponse } from '../../../api';
import { EntityDataStrategy } from './entity-data.strategy';

@Injectable({ providedIn: 'root' })
export class ClientDataStrategy implements EntityDataStrategy<ClientResponse> {
  private readonly accountsService = inject(AccountsService);

  async fetchById(id: string): Promise<ClientResponse> {
    try {
      return await firstValueFrom(this.accountsService.getClient(id));
    } catch (error) {
      throw new Error(
        `Failed to fetch client: ${error instanceof Error ? error.message : String(error)}`,
      );
    }
  }

  async update(
    id: string,
    data: Partial<ClientResponse>,
  ): Promise<ClientResponse> {
    try {
      return await firstValueFrom(this.accountsService.updateClient(id, data));
    } catch (error) {
      throw new Error(
        `Failed to update client: ${error instanceof Error ? error.message : String(error)}`,
      );
    }
  }
}
