import { Injectable, inject } from '@angular/core';
import { firstValueFrom } from 'rxjs';
import { AccountsService, DistributorResponse } from '../../../api';
import { EntityDataStrategy } from './entity-data.strategy';

@Injectable({ providedIn: 'root' })
export class DistributorDataStrategy
  implements EntityDataStrategy<DistributorResponse>
{
  private readonly accountsService = inject(AccountsService);

  async fetchById(id: string): Promise<DistributorResponse> {
    try {
      return await firstValueFrom(this.accountsService.getDistributor(id));
    } catch (error) {
      throw new Error(
        `Failed to fetch distributor: ${error instanceof Error ? error.message : String(error)}`,
      );
    }
  }

  async update(
    id: string,
    data: Partial<DistributorResponse>,
  ): Promise<DistributorResponse> {
    try {
      return await firstValueFrom(
        this.accountsService.updateDistributor(id, data),
      );
    } catch (error) {
      throw new Error(
        `Failed to update distributor: ${error instanceof Error ? error.message : String(error)}`,
      );
    }
  }
}
